export default {
	data() {
		return {
			currentIndex: 0,
			news: [],
			swiperHeight: 0,
			orderList: [],
			page: 1,
			flag: false,
			isNull: false
		}
	},
	onLoad() {
		this.getOrderMessages()
	},
	methods: {
		chooseOptions(index) {
			this.page = 1
			this.flag = false
			this.currentIndex = index
			if (this.currentIndex == 0) {
				this.getOrderMessages()
			} else {
				this.getSystemMessage()
			}
		},
		toOrderDetail(orderid) {
			uni.navigateTo({
				url: "../orderDetail/orderDetail?orderid=" + orderid
			})
		},
		getOrderMessages() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/Message/messageOrder",
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id')
				},
				method: 'POST',
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						if (res.data.data.length == 0) {
							this.isNull = true
							return
						}
						this.orderList = res.data.data
						for (let i = 0; i < this.orderList.length; i++) {
							if (this.orderList[i].is_read == 2) {
								this.changeReadStatus(this.orderList[i].id)
							}
						}
						return
					}
					this.showToast(res.data.msg)
				},
				fail: () => {
					this.showToast('系统繁忙，请稍后重试！')
				}
			})
		},
		getSystemMessage() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/Message/messageSystem",
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id')
				},
				method: 'POST',
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						if (res.data.data.length == 0) {
							this.isNull = true
							return
						}
						this.news = res.data.data
						for (let i = 0; i < this.news.length; i++) {
							if (this.news[i].is_read == 2) {
								this.changeReadStatus(this.news[i].id)
							}
						}
						return
					}
					this.showToast(res.data.msg)
				},
				fail: () => {
					this.showToast('系统繁忙，请稍后重试！')
				}
			})
		},
		changeReadStatus(id) {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/Message/alreadyRead",
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id'),
					id: id,
					type: this.currentIndex == 0 ? 1 : 2
				},
				method: 'POST',
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.news = res.data.data
						return
					}
					this.showToast(res.data.msg)
				},
				fail: () => {
					this.showToast('系统繁忙，请稍后重试！')
				}
			})
		},
		loadmore() {
			if (this.flag) {
				return
			}
			this.page++
			this.setheader()
			let url
			if (this.currentIndex == 0) {
				url = this.websiteUrl + "/api/Message/messageOrder"
			} else {
				url = this.websiteUrl + "/api/Message/messageSystem"
			}
			uni.request({
				url: url,
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id'),
					page: this.page
				},
				method: 'POST',
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						if (res.data.data.length == 0) {
							this.flag = true
							return
						}
						for (let i = 0; i < res.data.data.length; i++) {
							if (res.data.data[i].is_read == 2) {
								this.changeReadStatus(res.data.data[i].id)
							}
						}
						this.orderList.concat(res.data.data)
						return
					}
					this.showToast(res.data.msg)
				},
				fail: () => {
					this.showToast('系统繁忙，请稍后重试！')
				}
			})
		}
	},
	onReachBottom() {
		this.loadmore()
	}
}
